import React from 'react'
import useStyle from './subMenuStyles'
function SubMenu() {
    const classes=useStyle()
    return (
        <div className={classes.root}> 
        <div className={classes.divIphone}>
            <a className={classes.aIphone}></a>
          <ul style={{listStyleType:'none'}}>
              <li>
              <a className={classes.aIphone2}> 6s</a>
              </li>
              <li>
              <a className={classes.aIphone2}>5</a>
              </li>
          </ul>
          </div>
          <div className={classes.divIphone}>
            <a className={classes.aIphone}></a>
          <ul style={{listStyleType:'none'}}>
              <li>
              <a className={classes.aIphone2}>  A</a>
              </li>
              <li>
              <a className={classes.aIphone2}> S</a>
              </li>
          </ul>
          </div>
          <div className={classes.divIphone}>
            <a className={classes.aIphone}></a>
          <ul style={{listStyleType:'none'}}>
              <li>
              <a className={classes.aIphone2}> 8</a>
              </li>
              <li>
              <a className={classes.aIphone2}>  7.2</a>
              </li>
          </ul>
          </div>
        </div>
    )
}

export default SubMenu
